import './App.css';
import { useState } from 'react';
import TextField from "@mui/material/TextField";
import Card from "@mui/material/Card";
import CardContent from '@mui/material/CardContent';
import { CardActionArea } from '@mui/material';
import LoadingButton from "@mui/lab/LoadingButton";
import SendIcon from '@mui/icons-material/Send';
import GitHubIcon from '@mui/icons-material/GitHub';
import Snackbar from '@mui/material/Snackbar';
import Typography from '@mui/material/Typography';

function App() {
  const [open, setOpen] = useState(false);
  const vertical = 'bottom';
  const horizontal = 'center';
  const [headerSize, setHeaderSize] = useState(false);
  const [input, setInput] = useState("");
  const [loading, setLoading] = useState(false);
  const [responseSet, setResponseSet] = useState([]);
  const [idx, setIdx] = useState(0);
  const [answers, setAnswers] = useState('');
  const [data, setData] = useState({
    prompt: "Say something funny!",
    temperature: 0.5,
    max_tokens: 64,
    top_p: 1.0,
    frequency_penalty: 0.0,
    presence_penalty: 0.0,
  });

  const fetchData = () => {
    setLoading(true);
    fetch("/v1/engines/text-curie-001/completions", {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${process.env.REACT_APP_OPENAI_SECRET}`,
      },
      body: JSON.stringify(data),
    })
      .then(response => {
        return response.json()
      })
      .then(a => {
        setAnswers(a.choices);
        setResponseSet([...responseSet, [data, a.choices, idx]]);
        setIdx(idx+1);
        setLoading(false);
      })
  }

  const handleKeyDown = (e) => {
    if (e.keyCode === 13 && !e.shiftKey){
      fetchData();
      e.preventDefault();
    }
  }

  const handleMouseOver = () => {
    setHeaderSize(true);
  }

  const handleMouseOut = () => {
    setHeaderSize(false);
  }

  const handleCopy = (textToCopy) => {
    navigator.clipboard.writeText(textToCopy);
    setOpen(true);
  }
  const handleClose = () => {
    setOpen(false);
  }

  return (
    <div className="App">

      <div className='header' onMouseOver={handleMouseOver} onMouseOut={handleMouseOut}>
        <div className='github-link'>
          <a href="https://github.com/RyanPerera" target="_blank" rel="noopener noreferrer">
            <GitHubIcon fontSize={headerSize ? "large" : "small"} sx={{ color: "white", transition: "ease 0.3s" }} />
          </a>
        </div>
      </div>

      <div className='page'>
        <h1>Shopify Challenge</h1>
        <TextField id="outlined-basic" label="Enter Prompt" width="100px" variant="outlined" multiline rows={3} value={input}
          onChange={(e) => {
            setInput(e.target.value);
            setData({ ...data, prompt: e.target.value })
          }} 
          onKeyDown={(e) => {handleKeyDown(e)}}/>
        <div className='send'>
          <LoadingButton width="100px" endIcon={<SendIcon />} loading={loading} loadingPosition="end" variant="contained"
            sx={{ backgroundColor: "#96bf48", textTransform: "none" }} disableElevation
            onClick={(e) => fetchData(e)} >
            Send
          </LoadingButton>
        </div>
      </div>

      <div className='responses'>
        {answers ? responseSet.map((r) => (

          <div className='cardTransition' key={r[2]} >
            <Card sx={{ transition: "ease 0.3s" }}>
              <CardActionArea
                onClick={() => handleCopy(r[1][0].text)}>
                <CardContent>
                  <Typography sx={{ fontSize: 12, fontStyle: "italic" }} color="text.secondary">
                    Your Prompt:
                  </Typography>
                  <Typography variant="body2" sx={{ paddingBottom: "15px", fontStyle: "italic" }}>
                    {r[0].prompt}
                  </Typography>
                  <Typography sx={{ fontSize: 12, fontStyle: "italic" }} color="text.secondary">
                    Response:
                  </Typography>
                  <Typography variant="body1" sx={{ paddingBottom: "15px" }}>
                    {r[1][0].text}
                  </Typography>
                </CardContent>
              </CardActionArea>
            </Card>
          </div>

        )) : ""}
      </div>

      <Snackbar
        anchorOrigin={{ vertical, horizontal }}
        open={open}
        onClose={handleClose}
        message="Response copied to clipboard!"
        key={'top-center'}
      />

    </div>
  );
}

export default App;
